header{
    height: 120vh;

}


.header_container{
text-align: center;
height: 100%;
position: relative;
}

.headerlogo{
    width: 100px;
    height: 100px;
    margin: auto;
}


.headerImg{
    border-radius: 10px;
}


/* socials */

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}



/* profile */



.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


@media screen and (max-width: 1024px) {
    .header{
        height: 68vh; 
    }
}

@media screen and (max-width: 600px) {
    .header {
        height: 100vh;
    }

    .header_socials, 
    .scroll_down{
        display: none;
    }
}






