.services_container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
}

.service {
    border-radius: 2rem;
    border: 1px solid transparent;
    height: fit-content;
    transition: var(--transition);
}

.service:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}

.service_head {
    padding: 2rem;
    border-radius: 2rem;
}

.service_head h3 {
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service_list {
    padding: 2rem;
}

.service_list li {
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.scissor {
    height: 50px;
    width: 50pxs;
}

.service_list p {
    font-size: 0.9rem;
}




@media screen and (max-width: 1024px) {
    .services_container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }

    .service {
        height: auto;
    }
}

@media screen and (max-width: 600px) {

    .services_container{
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
}
